import React from 'react'
import { Element } from 'react-scroll'
import Topportfolio from './Topportofolio/Topportofolio'
import Bottomportfolio from './Bottomportfolio/Bottomportfolio'

const portfolio = () => {
  return (
    <Element name="portfolio" className="topcontainer">
       <Topportfolio />
       <Bottomportfolio />
    </Element>
  )
}

export default portfolio
