import React from 'react'
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import img1 from '../../../assests/Design.png'
import img2 from '../../../assests/Sketch.png'
import img3 from '../../../assests/Testing.png'
import './Middleabout.css'

const Middleabout = () => {
  return (
    <div className="middleabout">
        <Grid className="middleabout__grid" container spacing={4}>
        <Grid item xs={4}>
          <Box className="design">
          <h3>Design</h3>
          <p>Lorm Spam Door Somith Door</p>
          <img src={img1} alt="" />
        </Box>
        </Grid>

        <Grid item xs={4}>
          <Box className="sketch">
          <h3>Sketch</h3>
          <p>Lorm Spam Door Somith Doors</p>
          <img src={img2} alt="" />
        </Box>
        </Grid>

        <Grid item xs={4}>
          <Box className="testing">
          <h3>Testing</h3>
          <p>Lorm Spam Door Somith Doors</p>
          <img src={img3} alt="" />
        </Box>
        </Grid>
        </Grid>
    </div>
  )
}

export default Middleabout
