import React from 'react'
import Experience from './Experience/Experience'
import './Bottomportfolio.css'

const Bottomportfolio = () => {
  return (
    <div className="bottomportfolio">
      <h1>Our Success</h1>
      <h4>View our portfolio in catagoris</h4>
      <div className="bottomportfolio__box">
        <Experience number="+137" title="Project Done" />
        <Experience number="+47" title="Happy Client" style={{backgroundColor: "#F449CF"}} />
        <Experience number="+34" title="Return Client" style={{backgroundColor: "#FFC83A"}} />
      </div>
    </div>
  )
}

export default Bottomportfolio
