import React from 'react'
import img1 from '../../assests/digital-x-logo.png'
import img2 from '../../assests/excel-brand-logo.png'
import img3 from '../../assests/hikvison.png'
import img4 from '../../assests/Logitech-logo-300x300.png'
import img5 from '../../assests/tp-link.png'
import './Company.css'

const Company = () => {
  return (
    <div className="company">
      <a href="www.google.com"><img src={img1} alt="" /></a>
      <a href="www.google.com"><img src={img2} alt="" /></a>
      <a href="www.google.com"><img src={img3} alt="" /></a>
      <a href="www.google.com"><img src={img4} alt="" /></a>
      <a href="www.google.com"><img src={img5} alt="" /></a>
    </div>
  )
}

export default Company
