import React from 'react'
import './Topcontent.css'
import Homeimg from '../../../assests/Home.png'
import EmojiPeopleIcon from '../../../assests/waving-hand.png'

const Topcontent = () => {
  return (
    <div className="topcontent">
        <div className="topcontent__container">
            <h1>Provide You best <br />
                Best Ui Service
            </h1>
            <p>Mobile App Design and Development Studio. You can hire us</p>
            <a href="www.google.com">
                <button><img src={EmojiPeopleIcon} alt="" />  Wave Me</button>
            </a>
        </div>
        <div className="topcontent__image">
            <img src={Homeimg} alt="" />
        </div>
      
    </div>
  )
}

export default Topcontent
