import React from 'react'
import Grid from '@mui/material/Grid';
import './Topportfolio.css'
import { Link } from 'react-scroll'
import img1 from '../../../assests/Our portfolio_01.png'
import img2 from '../../../assests/Our portfolio_02.png'
import img3 from '../../../assests/Our portfolio_03.png'
import img4 from '../../../assests/Our portfolio_04.png'
import img5 from '../../../assests/Our portfolio_05.png'
import img6 from '../../../assests/Our portfolio_06.png'

const Topportofolio = () => {
  return (
    <div className="topportfolio">
        <Grid className="topportfolio__grid" container spacing={12}>
        <Grid className="topportfolio__grid0" item xs={4}>
            <div className="topportfolio__word">
                <h2>Our Portfolio</h2>
                <h4>View our portfolio in catagoris</h4></div>
                <div className="actionportfolio">
                <Link to="allworks" smooth={true} duration={500}>
                    <h4>All Works</h4>
                </Link>
                <Link to="minimaldesign" smooth={true} duration={500}>
                    <h4>Minimal Design</h4>
                </Link>
                <Link to="colorfulldesign" smooth={true} duration={500}>
                    <h4>Colorfull Design</h4>
                </Link>
                <Link to="landingpagedesign" smooth={true} duration={500}>
                    <h4>Landing Page Design</h4>
                </Link>
                <Link to="mobileappdesign" smooth={true} duration={500}>
                    <h4>Mobile App Design</h4>
                </Link>
                </div>
        </Grid>
        <Grid className="topportfolio__grid1" item xs={4}>
        <img src={img1} alt="" />
        <img src={img2} alt="" />
        <img src={img3} alt="" />
        
        </Grid>
        <Grid className="topportfolio__grid2" item xs={4}>
        <img src={img4} alt="" />
        <img src={img5} alt="" />
        <img src={img6} alt="" />
        </Grid>
        </Grid>
    </div>
  )
}

export default Topportofolio
